<style lang="less" scoped>
#myDrawer-search {
  button {
    color: #fff;
    background: #596e86;
    border: #596e86;
  }
}
#myDrawer-result {
  button {
    color: #596e86;
    background-color: transparent;
    border-color: #596e86;
  }
}
</style>

<template>
  <a-drawer title="选择经销商" width=500 placement="right" :closable="false" @close="onClose" :visible="visible">
    <a-card id='myDrawer-search'>
      <a-form>
        <a-form-item :labelCol="{ span: 7 }" :wrapperCol="{ span: 16 }" label='经销商代码'>
          <a-input placeholder='经销商代码' />
        </a-form-item>
        <a-form-item :labelCol="{ span: 7} " :wrapperCol="{ span: 16 }" label='经销商名称'>
          <a-input placeholder='经销商名称' />
        </a-form-item>
        <a-form-item style="text-align:right;">
          <div class="ant-col-23">
            <a-button type="primary" @click="showDrawer">查询</a-button>
          </div>
        </a-form-item>
      </a-form>
    </a-card>
    <a-card id="myDrawer-result">
      <a-table :columns="columns" :dataSource="data">
        <span slot="action" slot-scope="text, record">
          <a-button type="primary" ghost>选择</a-button>
        </span>
      </a-table>
    </a-card>
  </a-drawer>
</template>
<script>
const columns = [
  {
    title: "经销商代码",
    dataIndex: "distributorCode",
    scopedSlots: { customRender: "distributorCode" }
  },
  {
    title: "经销商名称",
    dataIndex: "distributorName"
  },
  {
    title: "选择",
    scopedSlots: { customRender: "action" }
  }
];
export default {
  data() {
    return {
      visible: false,
      columns,
      data: [{}]
    };
  },
  methods: {
    showDrawer() {
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    }
  }
};
</script>
