<style lang="less" scoped>
#createOrEdit {
  button {
    color: #fff;
    background: #596e86;
    border: #596e86;
  }
  #submit-btn-group {
    button {
      margin: auto 5px;
      width: 100px;
    }
  }
}
</style>

<template>
  <div id="createOrEdit">
    <a-card title="基本信息">
      <a @click='onRouter("accounting_bonusdata")' slot="extra">
        <a-icon type="rollback" />
      </a>
      <a-form>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='经销商代码'>
          <a-row>
            <a-col span='20'>
              <a-input placeholder='经销商代码' disabled />
            </a-col>
            <a-col span='4' style="text-align:right;">
              <a-button type="primary" @click="showDrawer">选择</a-button>
            </a-col>
          </a-row>
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='经销商名称'>
          <a-input placeholder='经销商名称' disabled />
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='奖励月份'>
          <a-input placeholder='奖励月份' />
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='VIN'>
          <a-input placeholder='VIN' />
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='奖励金类型'>
          <a-select defaultValue="" @change="handleChange">
            <a-select-option value="Operation Quality">Operation Quality</a-select-option>
            <a-select-option value="Basic Incentive">Basic Incentive</a-select-option>
            <a-select-option value="F Plan">F Plan</a-select-option>
            <a-select-option value="Insurance Incentive">Insurance Incentive</a-select-option>
            <a-select-option value="HV Campaign Promotion">HV Campaign Promotion</a-select-option>
            <a-select-option value="Regular- Demo Car">Regular- Demo Car</a-select-option>
            <a-select-option value="Regular-Loyalty">Regular-Loyalty</a-select-option>
            <a-select-option value="Regular-BJ Scrapage">Regular-BJ Scrapage</a-select-option>
            <a-select-option value="Regular-Fleet">Regular-Fleet</a-select-option>
            <a-select-option value="Trade in">Trade in</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='奖励金项目'>
          <a-select defaultValue="" @change="handleChange">
            <a-select-option value="华南地区NX推广">华南地区NX推广</a-select-option>
            <a-select-option value="华北地区ES推广">华北地区ES推广</a-select-option>
            <a-select-option value="华东地区LX推广">华东地区LX推广</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='奖励金额'>
          <a-input placeholder='奖励金额' />
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='未税金额'>
          <a-input placeholder='未税金额' />
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='税额'>
          <a-input placeholder='税额' />
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='税率'>
          <a-input placeholder='税率' />
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='凭证文件名'>
          <a-input placeholder='凭证文件名' />
        </a-form-item>
        <a-form-item :labelCol="formItemLayout.labelCol" :wrapperCol="formItemLayout.wrapperCol" label='凭证号'>
          <a-input placeholder='凭证号' />
        </a-form-item>
        <div id='submit-btn-group' style="text-align:center;">
          <a-button type="primary">提交</a-button>
          <a-button type="primary">重置</a-button>
        </div>
      </a-form>
    </a-card>
    <SelectDrawer ref="SelectDrawer" />
  </div>
</template>
<script>
const formItemLayout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 8 }
};
import SelectDrawer from "./components/selectDrawer";
export default {
  data() {
    return {
      formItemLayout
    };
  },
  components: {
    SelectDrawer: SelectDrawer
  },
  methods: {
    showDrawer() {
      this.$refs.SelectDrawer.showDrawer();
    },
    handleChange() {},
    onRouter(to) {
      this.$router.push({ name: to });
    }
  }
};
</script>

